const RightSection = (props) => {
    return (
        <div className="right-section">
            <a className="nav-side-option selected" onClick={() => {props.scrollToPage(0)}}></a>
            <a className="nav-side-option" onClick={() => {props.scrollToPage(1)}}></a>
            <a className="nav-side-option" onClick={() => {props.scrollToPage(2)}}></a>
            <a className="nav-side-option" onClick={() => {props.scrollToPage(3)}}></a>
            <a className="nav-side-option" onClick={() => {props.scrollToPage(4)}}></a>
        </div>
    )
}

export default RightSection

