import React, { useState } from "react"

const MobileNav = (props) => {
    const [menuOpen, setMenuOpen] = useState(false)

    const toggleMenu = (e) => {
        const target = document.querySelector(".menu-btn")
        if (target.classList.contains("open")) {
            target.classList.remove("open")
            setMenuOpen(false)
        } else {
            target.classList.add("open")
            setMenuOpen(true)
        }
    }

    return (
        <React.Fragment>
            <div className="mobile-nav" onClick={toggleMenu}>
                <div className="menu-btn">
                    <div className="menu-btn__burger"></div>
                </div>
            </div>
            {(menuOpen) ?
                <div className="nav-menu">
                    <div className="mobile-nav-option" onClick={() => {props.scrollToPage(1);toggleMenu()}}>about</div>
                    <div className="mobile-nav-option" onClick={() => {props.scrollToPage(2);toggleMenu()}}>skills</div>
                    <div className="mobile-nav-option" onClick={() => {props.scrollToPage(3);toggleMenu()}}>projects</div>
                </div>
            : <React.Fragment></React.Fragment>}
        </React.Fragment>
    )
}

export default MobileNav