import { useEffect } from "react"
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y, EffectCube } from "swiper";
import ImgToChar from "./ImgToChar"
import Spacious from "./Spacious"
import ECommerce from "./ECommerce"
import ComputerGraphics from "./ComputerGraphics"
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-cube';

const Projects = () => {
  useEffect(() => {
    const swiper = document.querySelector('.swiper').swiper;
    // document.querySelector('.swiper').onmouseover
    // setInterval(() => { swiper.slideNext(1300) }, 15000)
  }, [])

  return (
      <div className="page" id="projects">
        <Swiper
          modules={[Navigation, Pagination, Scrollbar, A11y, EffectCube]}
          effect="cube"
          // loop={true}
          cubeEffect={{ slideShadows: false, shadowScale: 1.1 }}
          speed={1300}
          // spaceBetween={0} // should be 0 when mobile view
          slidesPerView={1}
          navigation
          pagination={{ clickable: false }}
          // onSwiper={(swiper) => console.log(swiper)}
          // onSlideChange={() => console.log('slide change')}
        >
          <SwiperSlide>
            <div className="my-slide">
              <Spacious />
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="my-slide">
              <ImgToChar />
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="my-slide">
              <ECommerce />
            </div>
          </SwiperSlide>
          <SwiperSlide>
            <div className="my-slide">
              <ComputerGraphics />
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
  )
}

export default Projects