import { useEffect } from "react"

const About = (props) => {
  useEffect(() => {
    document.querySelectorAll("#about .jelly-text").forEach(text => {
      let startTimeout = false
      text.onmouseover = () => {
        if (!startTimeout) {
          startTimeout = true
          text.classList.add("jelly-animation")
          setTimeout(() => {
            text.classList.remove("jelly-animation")
            startTimeout = false
          }, 1000)
        }
      }
    })
  }, [])

  return (
      <div className="page" id="about">
        <div>
          <h1 className="jelly-text">I</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">a</h1>
          <h1 className="jelly-text">m</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">a</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">s</h1>
          <h1 className="jelly-text">o</h1>
          <h1 className="jelly-text">f</h1>
          <h1 className="jelly-text">t</h1>
          <h1 className="jelly-text">w</h1>
          <h1 className="jelly-text">a</h1>
          <h1 className="jelly-text">r</h1>
          <h1 className="jelly-text">e</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">e</h1>
          <h1 className="jelly-text">n</h1>
          <h1 className="jelly-text">g</h1>
          <h1 className="jelly-text">i</h1>
          <h1 className="jelly-text">n</h1>
          <h1 className="jelly-text">e</h1>
          <h1 className="jelly-text">n</h1>
          <h1 className="jelly-text">r</h1>
          <h1 className="jelly-text">.</h1>
        </div>
        <p>
          I create various apps from <span className="highlight-primary">full-stack</span> websites, to
          <span className="highlight-primary"> softwares</span> and even <span className="highlight-primary">games</span>. At the mean time
          making sure it is <span className="highlight-primary">visually appealing</span> and <span className="highlight-primary">lighting fast</span>
        </p>
        <a id="skills-btn" className="btn-1-reverse" href="#skills" onClick={() => {props.scrollToPage(2)}}>See my skill base</a>
      </div>
  )
}

export default About