import { useEffect } from "react"
import MobileNav from "./MobileNav"

const Navbar = (props) => {
    return (
        <div className="navbar">
            <div className="logo" style={{cursor: "pointer"}} onClick={() => {props.scrollToPage(0)}}>
                <label style={{cursor: "pointer"}}>D</label><label style={{cursor: "pointer"}}>L</label>
            </div>
            <div>
                <div className="nav-option" onClick={() => {props.scrollToPage(1)}}>about</div>
                <div className="nav-option" onClick={() => {props.scrollToPage(2)}}>skills</div>
                <div className="nav-option" onClick={() => {props.scrollToPage(3)}}>projects</div>
            </div>
            <MobileNav scrollToPage={props.scrollToPage}/>
        </div>
    )
}

export default Navbar