import React, { useEffect } from "react"
import "./stylesheets/_main.scss"
import Skills from "./components/Skills"
import Navbar from "./components/Navbar"
import Contact from "./components/Contact"
import Projects from "./components/Projects"
import About from "./components/About"
import Home from "./components/Home"
import LeftSection from "./components/LeftSection"
import RightSection from "./components/RightSection"
import HttpsRedirect from 'react-https-redirect'

const App = () => {
  let page = 0

  const enableSmoothScroll = () => {
    // scroll animation
    const middleSection = document.querySelector(".middle-section")
    const pages = [...document.querySelectorAll(".page")]

    const animate = () => {
      const target = middleSection.scrollTop

      pages.forEach((p, i) => {
        const top = p.offsetTop - middleSection.offsetTop // distance between top of view and page
        const offset = (top - target < 40 && top - target > -40) ? 0 : top - target // avoid inaccurate offset between top and scrollTop of view
        p.style.transform = `rotatey(${offset*.1}deg)`
      })

      requestAnimationFrame(animate)
    }
    animate()
    
    // page scrolling
    let scrolling = false
    let wheelCount = 0

    // // touch scroll
    // let prevY, dy, sy = 0
    // middleSection.ontouchmove = (e) => {
    //   if (prevY == undefined) {
    //     prevY = e.touches[0].screenY
    //     return
    //   }
      
    //   dy = e.touches[0].screenY - prevY
    //   prevY = e.touches[0].screenY
    //   sy += dy
    //   // console.log(sy, dy)
    //   if (sy > 0 && dy < 0) {
    //     sy = 0
    //   }
    //   if (sy < 0 && dy > 0) {
    //     sy = 0
    //   }
      
    //   let deltaY;
    //   // console.log(sy)
    //   if (Math.abs(sy) > 80) {
    //     deltaY = sy / Math.abs(sy)
    //     sy = 0
    //   } else {
    //     return
    //   }
      
    //   if (!scrolling) {
    //     scrolling = true
    //     setTimeout(() => {scrolling = false}, 1000)

    //   const pageHeight = document.querySelector(".page").clientHeight
    //     page += deltaY
    //     page = (page < 0) ? 0 : (page >= 4) ? 4 : page // limit page
    //     middleSection.scrollTo({
    //       top: pageHeight * page,
    //       left: 0,
    //       behavior: 'smooth'
    //     })
    //   }
    // }

    // mouse scroll
    middleSection.onwheel = (e) => {
      wheelCount ++
      if (wheelCount < 2) return
      wheelCount %= 2

      if (!scrolling) {
        scrolling = true
        setTimeout(() => {scrolling = false}, 1000)

      const pageHeight = document.querySelector(".page").clientHeight
        page += (e.deltaY > 0) ? 1 : -1 // change page depends on wheel up or down
        page = (page < 0) ? 0 : (page >= 4) ? 4 : page // limit page
        middleSection.scrollTo(0, pageHeight * page)
      }
    }
  }

  const enableSideNav = () => {
    const middleSection = document.querySelector(".middle-section")
    middleSection.onscroll = () => {
      document.querySelectorAll(".nav-side-option").forEach((sideOption, i) => {
        sideOption.classList.remove("selected")
        if (page == i) {
          sideOption.classList.add("selected")
        }
      })
    }
  }

  const scrollToPage = p => {
    const middleSection = document.querySelector(".middle-section")
    const pageHeight = document.querySelector(".page").clientHeight
    page = p
    middleSection.scrollTo(0, pageHeight * page)
  }

  useEffect(() => {
    enableSmoothScroll()
    enableSideNav()
  }, [])

  return (
    <HttpsRedirect>
      <img className="bg-load-animation" src="images/wallpapers/cow.jpg" onLoad={(e) => { setTimeout(() => { e.target.style.display = "none" }, 2000) }}/>
      <LeftSection scrollToPage={scrollToPage}/>
      <RightSection scrollToPage={scrollToPage}/>    

      <div className="middle-section">
        <Home scrollToPage={scrollToPage}/>
        <About scrollToPage={scrollToPage} />
        <Skills scrollToPage={scrollToPage} />
        <Projects />
        <Contact />
      </div>

      <Navbar scrollToPage={scrollToPage}/>
    </HttpsRedirect>
  )
}

export default App