import { useEffect, useState } from "react"
import * as THREE from "three"
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js'
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js'
import { randFloat, randInt } from "three/src/math/MathUtils"

const Skills = (props) => {
  useEffect(() => {
    // THREE JS
    const middleSection = document.querySelector(".middle-section")
    const scene = new THREE.Scene()

    const camera = new THREE.PerspectiveCamera(75, middleSection.clientWidth / middleSection.clientHeight, 0.1, 1000)

    const renderer = new THREE.WebGLRenderer({
      canvas: document.getElementById("mycanvas"),
      alpha: true
    })

    renderer.setPixelRatio( window.devicePixelRatio )
    renderer.setSize( middleSection.clientWidth, middleSection.clientHeight )
    camera.position.setZ(44)

    const material = new THREE.MeshStandardMaterial( { color: 0xdaf4fd })

    const pointLight = new THREE.PointLight(0xffffff)
    pointLight.intensity = 1
    pointLight.position.set(0, 0, 100)

    const lightHelper = new THREE.PointLightHelper(pointLight)
    const gridHelper = new THREE.GridHelper()
    
    scene.add(pointLight)
    // scene.add(lightHelper)
    scene.add(gridHelper)

    // const controls = new OrbitControls(camera, renderer.domElement)

    // TEXT LOADING
    const loader = new FontLoader();

    // detect for mobile size
    let isMobileSize = false
    if (window.innerWidth < 768) isMobileSize = true

    loader.load('font/Bolt_Regular.json', (font) => {
      const textGeometries = Array()
      const textMeshes = Array()
      const textParams = {
        font: font,
        size: (isMobileSize) ? 1.4 : 2.2,
        height: 0,
      }

      textGeometries.push(new TextGeometry("REACT JS", textParams))
      textGeometries.push(new TextGeometry("C++ / C", textParams))
      textGeometries.push(new TextGeometry("JAVA", textParams))
      textGeometries.push(new TextGeometry("PYTHON", textParams))
      textGeometries.push(new TextGeometry("NODE.JS", textParams))
      textGeometries.push(new TextGeometry("MONGO DB", textParams))
      textGeometries.push(new TextGeometry("SQL", textParams))
      textGeometries.push(new TextGeometry("SASS", textParams))   
      textGeometries.push(new TextGeometry("LINUX", textParams))
      textGeometries.push(new TextGeometry("GIT", textParams))
      textGeometries.push(new TextGeometry("CSS", textParams)) 
      textGeometries.push(new TextGeometry("HTML", textParams))
      textGeometries.push(new TextGeometry("TYPESCRIPT", textParams))
      textGeometries.push(new TextGeometry("VUE", textParams))
      textGeometries.push(new TextGeometry("DOCKER", textParams)) 
      textGeometries.push(new TextGeometry("PHP", textParams))
      textGeometries.push(new TextGeometry("WASM", textParams)) 
      textGeometries.push(new TextGeometry("EXPRESS", textParams))
 
      const rotateZ = (x, y, z, theta) => {
        return [x*Math.cos(theta)-y*Math.sin(theta), x*Math.sin(theta)+y*Math.cos(theta), z]
      }

      const rotateY = (x, y, z, theta) => {
        return [x*Math.cos(theta)+z*Math.sin(theta), y, -x*Math.sin(theta)+z*Math.cos(theta)]
      }

      const rotateX = (x, y, z, theta) => { 
        return [x, y*Math.cos(theta)-z*Math.sin(theta), y*Math.sin(theta)+z*Math.cos(theta)]
      }

      const degreePerRadian = 360 / (2 * Math.PI)

      const initialRotation = [ 
        [45/degreePerRadian, 135/degreePerRadian],
        [45/degreePerRadian, -135/degreePerRadian],
        [45/degreePerRadian, 45/degreePerRadian],
        [45/degreePerRadian, -45/degreePerRadian],
        [-45/degreePerRadian, 135/degreePerRadian],
        [-45/degreePerRadian, -135/degreePerRadian],
        [-45/degreePerRadian, 45/degreePerRadian],
        [-45/degreePerRadian, -45/degreePerRadian],
        [0/degreePerRadian, 90/degreePerRadian],
        [0/degreePerRadian, -90/degreePerRadian],
        [0/degreePerRadian, 180/degreePerRadian], 
        [0/degreePerRadian, 0/degreePerRadian],
        [90/degreePerRadian, 0/degreePerRadian],
        [0/degreePerRadian, 45/degreePerRadian], 
        [270/degreePerRadian, 0/degreePerRadian],
        [0/degreePerRadian, -45/degreePerRadian], 
        [0/degreePerRadian, 225/degreePerRadian], 
        [0/degreePerRadian, -225/degreePerRadian], 
      ]

      let offsetX, offsetY, offsetZ

      const box = new THREE.Box3()
      textGeometries.forEach((textGeometry, i) => {
        let textMesh = new THREE.Mesh( textGeometry, material)
        textMesh.geometry.computeBoundingBox()
        box.copy( textMesh.geometry.boundingBox ).applyMatrix4( textMesh.matrixWorld )

        offsetX = - box.max.x / 2 // x -12 - 12 y -13 - 13 z -17 - 17
        offsetY = - box.max.y / 2
        offsetZ = 0
        const radius = (isMobileSize) ? 13 : 19
        let x = 0, y = 0, z = radius
        
        let thetaX = initialRotation[i][0]
        let thetaY = initialRotation[i][1]

        let result
        result = rotateX(x, y, z, thetaX)
        x = result[0]
        y = result[1]
        z = result[2]
        result = rotateY(x, y, z, thetaY)
        x = result[0]
        y = result[1]
        z = result[2]

        x += offsetX
        y += offsetY
        z += offsetZ
        textMesh.position.x = x
        textMesh.position.y = y
        textMesh.position.z = z  //randFloat(10, 17) * (randInt(0, 1) ? 1 : -1) + 
        textMeshes.push(textMesh)
      })

      textMeshes.forEach(textMesh => {
        scene.add(textMesh)
      })

      // GET MOUSE DIRECTION
      let directionX = 1, directionY = 1
      const originX = window.innerWidth / 2
      const originY = window.innerHeight / 2
      document.addEventListener("mousemove", (e) => {
        directionX = isNaN((e.clientX - originX)) ? directionX : (e.clientX - originX)
        directionY = isNaN((e.clientY - originY)) ? directionY : (e.clientY - originY)
        directionX = (Math.abs(directionX / 10) > 20) ? (directionX / 10) / Math.abs(directionX / 10) * 20 : (directionX / 10)
        directionY = (Math.abs(directionY / 10) > 20) ? (directionY / 10) / Math.abs(directionY / 10) * 20 : (directionY / 10)
      })
      document.addEventListener("touchmove", (e) => {
        const x = e.touches[0].clientX
        const y = e.touches[0].clientY
        directionX = isNaN((x - originX)) ? directionX : (x - originX)
        directionY = isNaN((y - originY)) ? directionY : (y - originY)
        directionX = (Math.abs(directionX / 10) > 20) ? (directionX / 10) / Math.abs(directionX / 10) * 20 : (directionX / 10)
        directionY = (Math.abs(directionY / 10) > 20) ? (directionY / 10) / Math.abs(directionY / 10) * 20 : (directionY / 10)
      })
      document.addEventListener("touchstart", (e) => {
        const x = e.touches[0].clientX
        const y = e.touches[0].clientY
        directionX = isNaN((x - originX)) ? directionX : (x - originX)
        directionY = isNaN((y - originY)) ? directionY : (y - originY)
        directionX = (Math.abs(directionX / 10) > 20) ? (directionX / 10) / Math.abs(directionX / 10) * 20 : (directionX / 10)
        directionY = (Math.abs(directionY / 10) > 20) ? (directionY / 10) / Math.abs(directionY / 10) * 20 : (directionY / 10)
      })

      function animate() {
        requestAnimationFrame( animate )
  
        
        textMeshes.forEach((textMesh, i) => {
          let x, y, z

          const rotationSpeed = .001
          const maxSpeed = .014
          let thetaX = directionY * rotationSpeed
          let thetaY = directionX * rotationSpeed
          thetaX = thetaX > maxSpeed ? thetaX / Math.abs(thetaX) * maxSpeed : thetaX
          thetaY = thetaY > maxSpeed ? thetaY / Math.abs(thetaY) * maxSpeed : thetaY
          box.copy( textMesh.geometry.boundingBox ).applyMatrix4( textMesh.matrixWorld )

          x = textMesh.position.x - offsetX
          y = textMesh.position.y - offsetY
          z = textMesh.position.z - offsetZ
          
          let result
          result = rotateX(x, y, z, thetaX)
          x = result[0]
          y = result[1]
          z = result[2]
          result = rotateY(x, y, z, thetaY)
          x = result[0]
          y = result[1]
          z = result[2]
          
          textMesh.position.x = x + offsetX
          textMesh.position.y = y + offsetY
          textMesh.position.z = z + offsetZ
        })
  
        renderer.render( scene, camera )
      }
  
      animate()
    })
  }, [])

  return (
      <div className="page" id="skills">
        <h3>My skills</h3>
        <canvas id="mycanvas"></canvas>
        <a className="btn-4" onClick={() => {props.scrollToPage(3)}}>projects</a>
      </div>
  )
}

export default Skills