import React, { useEffect, useState } from "react"

const Contact = () => {
  const [formSent, setFormSent] = useState(false)

  useEffect(() => {
    document.querySelectorAll("#contact .jelly-text").forEach(text => {
      let startTimeout = false
      text.onmouseover = () => {
        if (!startTimeout) {
          startTimeout = true
          text.classList.add("jelly-animation-2")
          setTimeout(() => {
            text.classList.remove("jelly-animation-2")
            startTimeout = false
          }, 1000)
        }
      }
    })
  }, [])

  const sendForm = () => {
    const inputs = document.querySelectorAll("#contact input, #contact textarea")
    let flag = true
    inputs.forEach(input => {
      if (input.value == "") flag = false
    })
    if (flag) {
      setFormSent(true)
      inputs.forEach(input => {
        input.value = ""
      })
      setTimeout(() => { setFormSent(false) }, 3000)
    }
  }

  return (
      <div className="page" id="contact">
        <div>
          <h1 className="jelly-text">D</h1>
          <h1 className="jelly-text">r</h1>
          <h1 className="jelly-text">o</h1>
          <h1 className="jelly-text">p</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">m</h1>
          <h1 className="jelly-text">e</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">a</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text big-d">e</h1>
          <h1 className="jelly-text big-d">m</h1>
          <h1 className="jelly-text big-d">a</h1>
          <h1 className="jelly-text big-d">i</h1>
          <h1 className="jelly-text big-d">l</h1>
          <h1 className="jelly-text">&nbsp;</h1>
          <h1 className="jelly-text">.</h1>
        </div>
        <form id="myForm">
          <div className="input-field"><input type="text" required/><label className="placeholder">your e-mail</label></div>
          <div className="input-field"><input type="text" required/><label className="placeholder">topic</label></div>
          <div className="input-field"><textarea rows="5" required></textarea><label className="placeholder">content</label></div>
          <a className="btn-3" onClick={sendForm}>Send</a>
          {formSent ? <p className="form-msg">E-mail has been sent</p> : <React.Fragment></React.Fragment>}
        </form>
      </div>
  )
}

export default Contact